<template>
  <transition
  name="custom-classes-transition"
  enter-active-class="animated zoomIn"
  leave-active-class="animated zoomOut">
    <div
      :class="[
        'grid-item',
        'grid-x-'+property.attrs.col,
        'grid-y-'+property.attrs.row,
        'grid-w-'+property.attrs.width,
        'grid-h-'+property.attrs.height,
        'grid_padding'
      ]"
      :style="{'z-index':property.attrs.zIndex}">
      <div class="sti-panel"
        :style="property.panelStyle"
        :class="{'boder-debugger': boderDebugger}">
        <div class="sti-panel_inner">
          <div v-if="property.isHideHead" class="sti-panel_header sti-panel_header_hide">
          </div>
          <div v-else class="sti-panel_header sti-panel_header_show">
            <div class="sti-panel_header_title">
              <span>{{property.title}}</span>
            </div>
          </div>
          <div class="sti-panel_body">
            <div v-if="property.id == 1"><One :dataValue="dataValue"/></div>
            <div v-if="property.id == 2"><Two :dataValue="dataValue"/></div>
            <div v-if="property.id == 3"><Three :dataValue="dataValue"/></div>
            <div v-if="property.id == 4"><RightOne :dataValue="dataValue"/></div>
            <div v-if="property.id == 5"><Five :dataValue="dataValue"/></div>
            <div v-if="property.id == 6"><Six/></div>
            <div v-if="property.id == 7"><RightTwo :dataValue="dataValue"/></div>
            <!-- <component :is="comp" :value="pValue" :dataValue="dataValue" :property="property"></component> -->
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import './animate.css'
import './style.less'
import One from '@/views/home/one'
import Two from '@/views/home/two'
// import Three from '@/views/home/three'
import Five from '@/views/home/five'
import Three from '@/views/home/three'
import Six from '@/views/home/six'
import RightTwo from '@/views/home/right-two'
import RightOne from "../../views/home/right-one/right-one"
export default {
  components: {
    One,
    Three,
    Five,
    Six,
    Two,
    RightTwo,
    RightOne
  },
  props: {
    property: {
      type: Object,
      default: {}
    },
    dataValue: {
      defautl: null
    },
    boderDebugger: {
      type: Boolean,
      defautl: false
    }
  },
  data() {
    return {
      hideIcon: 0,
      currentComp: this.property.component,
      pValue: {
        'pCfg': this.property
      },
      des: '',
      toggle: true,
      cfgArray: []
    }
  },
  methods: {
  },
  watch: {
    property(v) {
      if (v) {
        this.currentComp = v.component
        this.pValue.pCfg = v
      }
    }
  },
  computed: {
    comp() {
      if (this.currentComp) {
        return require('../' + this.currentComp + '/index.vue').default
      }
    }
  },
  created() {
    // console.log(this.dataValue)
  },
  mounted() {}
}
</script>
