<template>
  <div class="six">
    <div class="top">
      <ul>
        <li v-for="(item, i) in tab" :key="i" :style="{background: item.color}" @click="search(item.title)">
          {{item.title}}
        </li>
      </ul>
    </div>
    <div class="bottom">
      <li>
        <span class="name">名称</span>
        <span class="type">类型</span>
        <span class="time">检测时间</span>
      </li>
      <vue-seamless-scroll :data="list" class="seamless-warp" :class-option="classOption">
        <ul>
          <li v-for="(item, i) in list" :key="i">
            <span class="name">{{item.malware_type}}</span>
            <span class="type">{{item.data_type}}</span>
            <span class="time">{{item.found_time}}</span>
          </li>
        </ul>
      </vue-seamless-scroll>
    </div>
  </div>
</template>

<script>

import data from '@/lcData.json'
import vueSeamlessScroll from 'vue-seamless-scroll'
import moment from 'moment'
export default {
  data () {
    return {
      tab: [
        {
          id: 0,
          title: '全部',
          color: '#f3e200'
        },
        {
          id: 1,
          title: '僵尸网络',
          color: '#1772c3'
        },
        {
          id: 2,
          title: '木马',
          color: '#00adac'
        },
        {
          id: 3,
          title: '蠕虫',
          color: '#04726f'
        },
        {
          id: 4,
          title: '病毒',
          color: '#b44379'
        },
        {
          id: 5,
          title: '其他',
          color: '#b29768'
        }
      ],
      list: data.stiffWoodMonitor
    }
  },
  components: {
    vueSeamlessScroll
  },
  methods: {
    search(title) {
      if(title === "全部"){
        this.list = data.stiffWoodMonitor
        console.log(this.list)
      } else if(title === "僵尸网络"){
        this.list = data.stiffWoodMonitor.filter((item, i) => {
          return item.data_type === '\u50f5\u5c38\u7f51\u7edc'
        })
      } else if(title === "木马"){
        this.list = data.stiffWoodMonitor.filter((item, i) => {
          return item.data_type === '\u6728\u9a6c'
        })
      } else if(title === "蠕虫"){
        this.list = data.stiffWoodMonitor.filter((item, i) => {

        })
      } else if(title === "病毒"){
        this.list = data.stiffWoodMonitor.filter((item, i) => {
          return item.data_type === '\u75c5\u6bd2'
        })
      } else if(title === "其他"){
        this.list = data.stiffWoodMonitor.filter((item, i) => {
          return item.data_type === '\u5176\u5b83'
        })
      }
    }
  },
  computed: {
    classOption () {
      return {
        step: 0.2, // 数值越大速度滚动越快
        limitMoveNum: 1, // 开始无缝滚动的数据量
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 100 // 单步运动停止的时间(默认值1000ms)
      }
    }
  },
  created () {
    this.list.map(item => {
      item.found_time = moment(item.found_time).format('YYYY年MM月DD日')
    })
  }
}
</script>

<style lang="scss">
  .six{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    .top{
      width: 100%;
      height: 40px;
      ul{
        display: flex;
        flex-direction: row;
        li{
          padding: 2px 12px;
          margin: 10px 2px;
          color:#000;
          border-radius: 10px;
        }
      }
    }
    .bottom{
      width: 100%;
      height: 230px;
      ul{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
      }
      li{
        width: 100%;
        height: 40px;
        line-height: 40px;
        display: flex;
        flex-direction: row;
        align-items: center;
        .name{
          width: 160px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          word-break: break-all;
          margin-left: 8px;
        }
        .type{
          width: 80px;
          margin: 0 10px;
        }
        .time{
          width: 140px;
        }
      }
    }
    .seamless-warp{
      width: 100%;
      height: 100%;
      overflow: hidden;
    }
    .active{
      background: cornsilk;
    }
  }
</style>
